<template>
  <div
    class="ds-platform-card-item-by-collection"
    :style="controlStyle"
    @click="_clickBefore(click)"
  >
    <template v-if="isDesign">
      <div class="cover">
        <el-image
          src="https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg"
          fit="cover"
        ></el-image>
      </div>
      <div
        class="title"
        :class="[lineNum < 2 ? 'ellipsis' : 'ellipsis-2']"
        :style="titleStyle"
      >
        
        
        
        
        
      </div>
      <!-- </div> -->
    </template>
    <template v-else>
      <div class="cover">
        <el-image :src="data['cover'] | imgFormat" fit="cover"></el-image>
      </div>
      <div
        class="title"
        :class="[lineNum < 2 ? 'ellipsis' : 'ellipsis-2']"
        :style="titleStyle"
      >
        {{ data["title"] }}
      </div>
    </template>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfPlatformCardItemByCollection",
  ctrlCaption: "",
  mixins: [$mixins.card],
  props: {
    //
    padding: {
      default: () => [0, 0, 0, 0],
    },
    //
    keysMap: {
      type: Array,
      default() {
        return [
          { from: "cover", to: "cover", type: "text", desc: "" },
          { from: "title", to: "title", type: "text", desc: "" },
        ];
      },
    },
    //
    lineNum: {
      type: Number,
      default: 2,
    },
    clickConfig: {
      type: Object,
      default: null,
    },
  },
  computed: {
    controlStyle() {
      let { padding, isDesign } = this;
      let style = {
        padding: padding.join("px ") + "px",
      };
      if (isDesign) {
        style.padding = "10px 10px 10px 10px";
      }
      return style;
    },
    titleStyle() {
      if (this.lineNum < 2) return {};
      return {
        "-webkit-line-clamp": this.lineNum,
        height: this.lineNum * 22 + "px",
      };
    },
  },
  methods: {
    click() {
      if (this.$listeners.itemClick) {
        this.$dispatch("itemClick", item);
      } else {
        if (!this.clickConfig) return;
        let { url, state } = this.clickConfig;
        url = this.$replace(url, this.local);
        this.$open({ url, state });
      }
    },
  },
});
</script>
