<template>
  <div v-if="isDesign" class="ds-platform-card-item-by-teacherlist">
    <div class="avatar-wrap">
      <el-image
        class="avatar"
        src="https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg"
        fit="cover"
      ></el-image>
    </div>
    <div class="info">
      <p class="name"></p>
      <p class="line-2"></p>
      <p class="course">15 ></p>
    </div>
  </div>
  <div v-else class="ds-platform-card-item-by-teacherlist">
    <div class="avatar-wrap">
      <el-image class="avatar" :src="data['avatar'] | imgFormat" fit="cover"></el-image>
    </div>
    <div class="info">
      <p class="name">{{ data["name"] }}</p>
      <p class="line-2">{{ data["unit"] }}{{ data["position"] }}</p>
      <p class="course" @click.stop="_clickBefore(courseClick)">
        {{ data["courseNum"] }}{{ getSuffix("courseNum") }} >
      </p>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfPlatformCardItemByTeacherList",
  ctrlCaption: "",
  mixins: [$mixins.card],
  props: {
    keysMap: {
      type: Array,
      default() {
        return [
          { from: "avatar", to: "avatar", type: "text", desc: "" },
          { from: "name", to: "name", type: "text", desc: "" },
          { from: "unit", to: "unit", type: "text", desc: "" },
          { from: "position", to: "position", type: "text", desc: "" },
          {
            from: "courseNum",
            to: "courseNum",
            type: "text|number",
            desc: "",
            suffix: "",
          },
        ];
      },
    },
    // 
    clickConfig: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    courseClick() {
      if (this.$listeners.courseClick) {
        this.$dispath("courseClick", this.local);
      } else {
        if (!this.clickConfig) return;
        let { url, state } = this.clickConfig;
        url = this.$replace(url, this.local);
        this.$open({ url, state });
      }
    },
  },
});
</script>
