<template>
  <div v-if="isDesign" class="ds-platform-card-item-by-course">
    <div class="cover">
      <el-image src="https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg"></el-image>
    </div>
    <div class="content">
      <div class="title ellipsis-2">
        <span style="color: #FF3600;font-weight: bold;margin-right: 5px">NEW</span>
        <span></span>
      </div>
    </div>
    <div class="footer">
      <span></span>
      <span><i class="iconfont icon-guankanbeifen4" style="font-size: .9em"></i> 1234</span>
    </div>
  </div>
  <div v-else class="ds-platform-card-item-by-course" @click="_clickBefore(click)">
    <div class="cover">
      <el-image :src="data['cover'] | imgFormat" fit="cover"></el-image>
    </div>
    <div class="content">
      <div class="title ellipsis-2">
        <span v-if="data['isNew']" style="color: #FF3600;font-weight: bold;margin-right: 5px">NEW</span>
        <span>{{ data['title'] }}</span>
      </div>
    </div>
    <div class="footer">
      <span class="ellipsis" :title="data['teacher']">{{ data['teacher'] }}</span>
      <span><i class="iconfont icon-guankanbeifen4" style="font-size: .9em"></i> {{ data['views'] || 0 }}</span>
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfPlatformCardItemByCourse",
  ctrlCaption: "",
  mixins: [$mixins.card],
  props: {
    keysMap: {
      type: Array,
      default() {
        return [
          { from: 'cover', to: 'cover', type: 'text', desc: '' },
          { from: 'isNew', to: 'isNew', type: 'boolean', desc: '' },
          { from: 'title', to: 'title', type: 'text', desc: '' },
          { from: 'teacher', to: 'teacher', type: 'text', desc: '' },
          { from: 'views', to: 'views', type: 'text|number', desc: '' }
        ];
      }
    },
    // 
    clickConfig: {
      type: Object,
      default: null
    }
  },
  methods: {
    click() {
      if (!this.clickConfig) return;
      let { url, state } = this.clickConfig;
      url = this.$replace(url, this.local);
      this.$open({ url, state });
    }
  }
});
</script>