<template>
  <el-dialog
    ref="dialog"
    :index="index"
    class="ds-dialog"
    :class="[footerAlignClassName,conentBorder?'ds-dialog-content-border':'']"
    v-bind.sync="$attrs"
    @closed="closeHandler"
    v-on="$listeners"
    append-to-body
  >
    <template v-slot:title>
      <slot name="title"></slot>
    </template>
    <template v-slot:default>
      <slot></slot>
    </template>
    <template v-slot:footer>
      <slot name="footer"></slot>
    </template>
  </el-dialog>
</template>
<script>
/**
 * @class DsfDialog
 * @description 
 * @example
 * <DsfDialog>
 *  <template slot="title"></template>
 *  <template></template>
 *  <template slot="footer"></template>
 * </DsfDialog>
 */
export default {
  /**
   * @memberof DsfDialog
   * @name Slots
   * @property {*} title 
   * @property {*} default 
   * @property {*} footer 
   */
  name: "DsfDialog",
  inheritAttrs: false,
  provide() {
    return {
      $dialog: this
    };
  },
  props: {
    index: {
      type: String,
      default: ""
    },
    options: {
      type: Object,
      default() {
        return {};
      }
    },
    dialogArgs: {
      type: Object,
      default() {
        return null;
      }
    },
    openerVm: {
      type: Object,
      default() {
        return null;
      }
    },
    // left,center,right
    footerAlign: {
      type: String,
      required: false
    },
    //
    conentBorder: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      currentView: null
    };
  },
  computed: {
    footerAlignClassName() {
      let align = this.footerAlign || dsf.config.setting_public_default_dialog_footer_align;
      return align && align != "right" ? "dialog-footer-" + align : "";
    }
  },
  mounted() {},
  created() {
    // debugger
  },
  beforeDestroy() {
    // debugger
  },
  methods: {
    closeHandler() {
      this.$emit("close");
    },
    close() {
      this.$emit("update:visible", false);
    }
  }
};
</script>
