<template>
  <div class="dsf-forgot-password-control">
    <!--  -->
    <div class="dsf-forgot-password-step">
      <div class="forgot-password-step" :class="{ 'current-step': step >= 1 }">
        <div class="step-cricle">
          <div class="cricle normal">1</div>
          <p></p>
        </div>
        <div class="right-line"></div>
      </div>
      <div class="forgot-password-step" :class="{ 'current-step': step >= 2 }">
        <div class="left-line"></div>
        <div class="step-cricle">
          <div class="cricle">2</div>
          <p></p>
        </div>
        <div class="right-line"></div>
      </div>
      <div class="forgot-password-step" :class="{ 'current-step': step >= 3 }">
        <div class="left-line"></div>
        <div class="step-cricle">
          <div class="cricle">3</div>
          <p></p>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="dsf-forgot-password-content">
      <!--  -->
      <div v-if="step == 1">
        <input
          type="text"
          v-model="phone"
          class="dsf-forgot-password-input"
          placeholder=""
        />
        <div class="dsf-forgot-password-btn" @click="showDialog">
          
        </div>
        <el-button
          type="primary"
          class="dsf-forgot-password-next-btn"
          :class="{ disabled: disabled }"
          :disabled="disabled"
        >
          
        </el-button>
      </div>
      <!--  -->
      <div v-if="step === 2">
        <p class="step-title"></p>
        <div class="dsf-forgot-password-input-btn">
          <input :type="type" v-model="newpwd" placeholder="" />
          <i
            class="icon iconfont"
            :class="{
              'icon-icon-invisible': type === 'password',
              'icon-icon-visible': type === 'text',
            }"
            @click="changeIcon"
          ></i>
        </div>
        <div class="dsf-forgot-password-input-btn">
          <input type="password" v-model="confirmNewpwd" placeholder="" />
        </div>
        <p class="step-tip">{{ notice }}</p>
        <el-button
          type="primary"
          @click="nextStep(3)"
          class="dsf-forgot-password-next-btn"
          :class="{ disabled: !newpwd||(newpwd&&newpwd!==confirmNewpwd)}"
          :disabled="!newpwd||(newpwd&&newpwd!==confirmNewpwd)"
        >
          
        </el-button>
      </div>
      <!--  -->
      <div class="step-three" v-if="step === 3">
        <div class="step-succss">
          <i class="el-icon-check" />
        </div>
        <p class="success-tip"></p>
        <el-button
          @click="reLogin"
          type="primary"
          class="dsf-forgot-password-success-btn"
        >
           
        </el-button>
      </div>
    </div>
    <el-dialog :modal-append-to-body="false" :visible.sync="dialogVisible" width="480px" :close-on-click-modal="false" :close-on-press-escape="false">
      <div class="forgot-password-dialog">
        <div class="title"></div>
        <div class="phone">{{ hiddenPhone }}</div>
        <div class="dsf-forgot-password-input-2">
          <input type="text" v-model="code" placeholder="" />
          <el-button
            type="primary"
            :class="{ disabled: interval }"
            :disabled="Boolean(interval)"
            @click="getCode"
            class="dsf-forgot-password-next-btn"
          >
            {{ timeMsg }}
          </el-button>
        </div>
        <p class="error-msg" v-if="codeAuthResult && code">
          <i class="iconfont icon-tixingshixin" />
          {{codeAuthResult}}
        </p>
        <el-button
          type="primary"
          :class="{ disabled: !code }"
          @click="nextStep(2)"
          :disabled="!code"
          class="dsf-forgot-password-dialog-next-btn"
        >
          
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfPlatformForgotPassword",
  ctrlCaption: "",
  mixins: [$mixins.control],
  data() {
    return {
      // 
      step: 1,
      //
      phone: "",
      disabled: true,
      // 
      dialogVisible: false,
      // 
      time: this.countdownTimes,
      //
      countdownTimes: 0,
      // 
      type: "password",
      // 
      newpwd: "",
      //
      confirmNewpwd:'',
      //
      notice:'',
      // 
      interval: null,
      // 
      code: "",
      // 
      isCodeSend: false,
      //
      codeAuthResult: '',
      //token
      token:'',
    };
  },
  props: {
    title:{
      type:String,
      default:''
    },
    intervalUrl:{
      type:String,
      default:'oua2/user/pwd/send/interval'
    },
    getCodeUrl:{
      type:String,
      default:'oua2/user/pwd/send'
    },
    authCodeUrl:{
      type:String,
      default:'oua2/user/pwd/token'
    },
    alterPwdUrl:{
      type:String,
      default:'oua2/user/pwd/update'
    }
  },
  computed: {
    timeMsg() {
      return this.interval
        ? this.time + " s": (this.isCodeSend? "": "");
    },
    hiddenPhone() {
      return this.phone.replace(/(\d{3})\d{4}(\d{4})/, "$1****$2");
    },
  },
  watch: {
    code(){
      this.codeAuthResult = ''
    },
    step(val) {
      if (val === 2) {
        this.dialogVisible = false;
      }
    },
    dialogVisible(val) {
      if (!val) {
        this.interval && clearInterval(this.interval);
        this.time = this.countdownTimes;
        this.interval = null;
      }
    },
  },
  mounted() {
    // 
    this.getInterval();
  },
  beforeDestroy () {
    this.interval && clearInterval(this.interval);
    this.time = this.countdownTimes;
    this.interval = null;
  },
  methods: {
    // 
    getInterval() {
      this.$http
        .get(this.intervalUrl, {}, $$webRoot.dsfa)
        .done(({ success, data, message }) => {
          if (success) {
            this.countdownTimes = data.interval;
            this.time = data.interval;
            this.notice = data.notice
          } else {
            dsf.layer.message(message || "", false);
          }
        })
        .error((err) => {
          dsf.error(err);
          dsf.layer.message(err?.message || "", false);
        })
        .always(() => {
        });
    },
    showDialog() {
      if(this.phone===''){
        dsf.layer.message("", false);
        return false
      }
      if (/^1(3|4|5|7|8|9|6)\d{9}$/.test(this.phone)) {
        this.codeAuthResult=''
        this.code=''
        this.dialogVisible = true;
      } else {
        dsf.layer.message("", false);
      }
    },
    changeIcon() {
      if (this.type === "text") {
        this.type = "password";
      } else {
        this.type = "text";
      }
    },
    async nextStep(step) {
      if(step===2){
        // 
        let {err,token} = await this.authCode()
        if(err){
          this.codeAuthResult = err
          return false
        }
        this.token = token
        this.step = step;
      }else if (step === 3) {
        let {err} = await this.alterPassword()
        if(err) return dsf.layer.message(err, false);
        this.step = step;
      }
      
    },
    // 
    getCode() {
      this.$http
        .post(this.getCodeUrl, { phone: this.phone }, $$webRoot.dsfa)
        .done(({ success, data, message }) => {
          if (success) {
            this.isCodeSend = true;
            dsf.layer.message("");
            this.interval = setInterval(() => {
              this.time--;
              if (this.time <= 0) {
                clearInterval(this.interval);
                this.interval = null;
                this.time = this.countdownTimes;
              }
            }, 1000);
          } else {
            dsf.layer.message(message || "", false);
          }
        })
        .error((err) => {
          dsf.error(err);
          dsf.layer.message(err?.message || "", false);
        })
        .always(() => {});
    },
    // 
    authCode(){
      return new Promise((resolve)=>{
        this.$http
          .get(this.authCodeUrl, { phone: this.phone,code:this.code }, $$webRoot.dsfa)
          .done(({ success, data }) => {
            if (success) {
              resolve({err:null,token:data})
            } else {
              resolve({err:data||'',token:null})
            }
          })
          .error((err) => {
            resolve({err:err?.message||'',token:null})
          })
          .always(() => {});
      })
     
    },
    // 
    alterPassword(){
      return new Promise((resolve)=>{
        this.$http
          .post(this.alterPwdUrl, { phone: this.phone,token:this.token,pwd:this.newpwd}, $$webRoot.dsfa)
          .done(({ success, data, message }) => {
            if (success) {
              resolve({err:null,token:data})
            } else {
              resolve({err:message||'',token:null})
            }
          })
          .error((err) => {
            resolve({err:err?.message||'',token:null})
          })
          .always(() => {});
      })
    },
    reLogin() {
      this.$dispatch('complete')
      this.$router.go(-1)
    },
  },
});
</script>
