<template>
  <div class="ds-component-info">
    <p v-if="info.caption"><label></label>{{ info.caption }}</p>
    <p v-if="info.id"><label></label>{{ info.id }}</p>
    <p v-if="info.name"><label></label>{{ info.name }}</p>
    <p v-if="info.typeText || info.typeValue"><label></label>{{ info.typeText || '' }} | {{ info.typeValue || '' }}</p>
  </div>
</template>

<script>
export default {
  name: "DsfComponentInfo",
  props: {
    info: {
      type: Object,
      default() {
        return {}
      }
    }
  }
}
</script>